@import '~antd/dist/antd.css';
@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

.App {
  text-align: center;
  background-color: #fff;
}

.App-header {
  height:8vh;
  background-color: #ffffff !important;
  border-bottom: #aaa 2px solid;
}

header img path {
  fill: #61dafb !important;
}

header .header-logo {
  padding: 10px;
  height: 100%;
  max-height:100%;
  width:auto;
  max-width: 100vw;
}

header .header-logo-container {
  height:100%;
  width:auto;
  max-width: 100vw;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-link {
  color: #61dafb;
}

.main-content {
  text-align: center;
  min-height: 90vh;
  overflow: hidden;
  display: flex;
}

.decision-area {
  flex-grow: 1;
  max-width: 100vw;
}

.decision-area button {
  margin-top: 15px !important;
  margin: 5px;
}

.sider {
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: #f8f8f8;
  overflow: hidden;
  min-width: fit-content;
  width: 100vw;
  height:90vh !important;
}

.card-container {
  display: flex;
  flex-grow: 1;
  padding-top: 5px;
  padding-bottom: 5px;
  max-width: fit-content;
  max-height: fit-content;
  transform: translateX(-50%) translateX(50vw);
}

.ant-card {
  margin-top: 15px;
  margin-bottom: 30px;
  margin-right: auto;
  margin-left: auto;
  overflow: hidden;
}

.embed-container {
  overflow: hidden;
  width: 584px !important;
  height:450px !important;
  transform: translateY(-1px);
}

.sider-details {
  flex-grow: 1;
  max-height: fit-content;
  padding: 15px;
  max-width: 100vw;
}

.sider-details h1 h2 h3 p {
  display: inline-block;
}

.ant-spin-container, .ant-spin-nested-loading {
  height: inherit;
  width: inherit;
}

.file-frame {
  pointer-events: none;
  border-width: 0px;
  transform: translateX(-3px) translateY(-58px);
  width: 590px !important;
  height:513px !important;
}